import React, { Fragment, useState, useCallback } from 'react';
import { Form, FormLayout, TextField, Page, Layout, Button } from '@shopify/polaris';
import Question from '../components/Question';

const Create = () => {

    const [url, setUrl] = useState('');

    const handleSubmit = useCallback((_event) => setUrl(''), []);

    const handleUrlChange = useCallback((value) => setUrl(value), []);

    return (
        <Fragment>
            <Page>

                <Form noValidate onSubmit={handleSubmit}>
                    <FormLayout>
                        <TextField
                            value={url}
                            onChange={handleUrlChange}
                            label="Name"
                            type="text"
                            placeholder="Help me decide, Find similar products, etc"
                        />
                        <Question />

                        <Button submit>Submit</Button>
                    </FormLayout>
                </Form>

            </Page>

        </Fragment>
    )
}

export default Create;